import React from "react";
import { Cell, Div, Link } from "@vkontakte/vkui";
import "../styles/components.sass";

const InventoryItem = ({
  id,
  name,
  icon,
  openUseItem,
  count,
  targetIdInventory,
  scheme,
}) => {
  return (
    <Div className={scheme === "bright_light" ? "cardBlack" : "cardWhite"}>
      <Cell
        className="myItem"
        before={icon}
        asideContent={
          <Div id={id} mode="tertiary">
            {count} шт
          </Div>
        }
        description={
          <Div id={id} onClick={openUseItem}>
            Использовать
          </Div>
        }
      >
        {name}
      </Cell>
    </Div>
  );
};

export { InventoryItem };
