import React, { useState, useEffect, useReducer } from "react";
import axios from "axios";
import bridge from "@vkontakte/vk-bridge";
import {
  Panel,
  PanelHeader,
  Div,
  Spinner,
  ActionSheet,
  ActionSheetItem,
  Button,
} from "@vkontakte/vkui";
import Icon16CheckCircle from "@vkontakte/icons/dist/16/check_circle";
import Icon16Smile from "@vkontakte/icons/dist/16/smile";

import requestMethod from "../helpers/requestMethod";
import { getRankByRating, formatCoins } from "../helpers";
import Logger from "../helpers/logger";
import VkButton from "../components/UI/VkButton";
import "../components/UI/VkButton/VkButton.sass";

// Loggers
const vk = new Logger("VK");

const Home = ({
  id,
  go,
  fetchedUser,
  activeTab,
  durkaCoins,
  shisoids,
  openTransfer,
  snackbar,
  rating,
  scheme,
  openBuyDurkaCoins,
  networkStatus,
  updateConnection,
  socketReconnection,
  socketReconnect,
  connectTwoDevices,
  stub,
  online,
  setPopout,
  openAlertPay,
  setActivePanel,
  openDurkaCoin
}) => {
  const [localScheme, setLocalScheme] = useState("bright_light");
  const [isMember, setIsMember] = useState(false);

  useEffect(() => {
    if (scheme) {
      setLocalScheme(scheme);
    }
  }, [scheme]);

  useEffect(() => {
    console.log("HEY FROM HOME props.scheme ===>", scheme);

    setTimeout(() => {
      addGroup(true);
    }, 5000);
  }, []);

  function openBase() {
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={shareLink}>
            Поделиться ссылкой
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={copyLink}>
            Скопировать ссылку
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={story}>
            Поделиться в истории
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={share}>
            Поделиться на стене
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function openDurkaCoin() {
    function goHistory() {
      setActivePanel("operations-history");
    }

    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={openBuyDurkaCoins}>
            Купить
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={openTransfer}>
            Перевести
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={goHistory}>
            История операций
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function openActions() {
    function goRegulations() {
      setActivePanel("how-it-works");
    }
    function goRankInfo() {
      setActivePanel("rank-info");
    }

    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={openBase}>
            Привлечь шизоидов
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={goRankInfo}>
            Звания
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={goRegulations}>
            Правила
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={addGroup}>
            Подписаться
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={AddToCommunity}>
            Добавить в группу
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function shareLink() {
    const link = `https://vk.com/app7351258#${fetchedUser.id}`;
    bridge.send("VKWebAppShare", {
      link: link,
    });
  }

  function story() {
    const url = `https://vk.com/app7351258#${fetchedUser.id}`;

    bridge
      .sendPromise("VKWebAppShowStoryBox", {
        background_type: "image",
        url:
          "https://sun9-28.userapi.com/c855732/v855732341/215725/RVJstbppzyk.jpg",
        attachment: {
          text: "go_to",
          type: "url",
          url: url,
        },
      })
      .then((res) => {
        if (res.data.result === true) {
          axios.post(`${requestMethod}/story`, {
            search: document.location.search,
          });
        }
      })
      .catch((error) => {
        vk.log("story error:", error);
      });
  }

  function AddToCommunity() {
    bridge.send("VKWebAppAddToCommunity", {});
  }

  function addGroup(fromUseEffect) {
    bridge
      .send("VKWebAppJoinGroup", { group_id: 192779261 })
      .then(({ result }) => {
        if (result && fromUseEffect !== true) {
          openAlertPay("Вы уже подписаны на группу");
        }
      });
  }

  function share(e) {
    //  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
    e.preventDefault();
    const url = `https://vk.com/app7351258#${fetchedUser.id}`;

    bridge.send("VKWebAppShowWallPostBox", {
      message: `А ты уже был в ДУРКЕ? Приложение - ${url}`,
      attachments: `photo73606509_457262018,https://vk.com/app7351258#${fetchedUser.id}`,
    });
  }

  // Копирование в буфер
  function copyLink() {
    const link = `https://vk.com/app7351258#${fetchedUser.id}`;
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        vk.log(result.platform);
        if (result.platform === "web" || result.platform === "mobile-web") {
          window.navigator.clipboard.writeText(link).then(
            () => {
              vk.log("successfully set", link);
              openAlertPay("Ссылка скопирована");
            },
            () => {
              vk.log("write failed", link);
            }
          );
        } else {
          bridge.send("VKWebAppCopyText", { text: link });
          openAlertPay("Ссылка скопирована");
        }
      })
      .catch((error) => {
        vk.log(error);
      });
  }

  return (
    <Panel id={id}>
      <PanelHeader left={
        <Div
          style={{
            display: "flex",
            alignItems: "start",
            justifyContent: "center",
            flexDirection: "column",
            marginLeft: 15,
            marginTop: 5,
            fontSize: 10,
            color: `${scheme === "bright_light" ? "black" :  "white"}`
          }}
        >
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "row",
              marginBottom: 5,
            }}
          >
            <Icon16Smile fill="green" style={{ marginRight: 8 }} />
            <Div>Привлечено: {shisoids}</Div>
          </Div>

          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "row",
            }}
          >
            <Icon16CheckCircle fill="green" style={{ marginRight: 8 }} />
            <Div>Онлайн: {online}</Div>
          </Div>
        </Div>
      }>
        
      </PanelHeader>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
        <>
          {networkStatus ? (
            stub(
              "Отсутствует интернет - соединение!",
              updateConnection,
              "Повторить"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub("Проводятся тех. работы", socketReconnect, "Повторить")
              ) : (
                <>
                  {!rating ? (
                    <div
                    className="spinner"
                    >
                      <Spinner size="large" style={{ marginTop: 90 }} />
                    </div>
                  ) : (
                    <>
                      {activeTab === "home" ? (
                        <>
                          <Div className="main">
                            

                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                flexDirection: "row",
                              }}
                            >
                              <Div>БАЛАНС</Div>
                            </Div>

                            <Div className="divFlexInfo">
                              <Div className="balanceDC">
                                {formatCoins(durkaCoins)}
                              </Div>
                              <Div
                                className="iconDCBig"
                                style={{ marginLeft: 4 }}
                              />
                            </Div>

                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                flexDirection: "row",
                                margin: 0,
                                marginBottom: 5,
                                padding: 0,
                              }}
                            >
                              РЕЙТИНГ
                            </Div>

                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                flexDirection: "row",
                              }}
                            >
                              <Div className="rating">{rating}</Div>
                              <Div className="iconStarBig" />
                            </Div>
                          </Div>

                          <Div className="divHomeButton">
                            {/* <VkButton
                              handleClick={openBase}
                              variant="Classic"
                              scheme={localScheme}
                            >
                              Привлечь шизоидов
                            </VkButton> */}

                            <Div className="flexBtn">
                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "column",
                                  justifyContent: "center",
                                }}
                              >
                                <Div>
                                  <Button
                                    onClick={openActions}
                                    className={
                                      localScheme === "bright_light"
                                        ? "Triangle bright_light"
                                        : "Triangle space_gray"
                                    }
                                  ></Button>
                                </Div>
                                <Div> Действия </Div>
                              </Div>

                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "column",
                                  justifyContent: "center",
                                }}
                              >
                                <VkButton
                                  handleClick={openDurkaCoin}
                                  variant="Circle"
                                  scheme={localScheme}
                                ></VkButton>
                                <Div> DurkaCoin </Div>
                              </Div>

                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "column",
                                  justifyContent: "center",
                                }}
                              >
                                <VkButton
                                  handleClick={go}
                                  id={id}
                                  data-to="inventory"
                                  variant="Square"
                                  scheme={localScheme}
                                ></VkButton>
                                <Div> Инвентарь </Div>
                              </Div>
                            </Div>
                          </Div>
                          {/* )} */}
                        </>
                      ) : (
                        ""
                      )}
                      {snackbar}
                    </>
                  )}
                </>
              )}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Home };
